import React, { useEffect, useState } from 'react';

import { NavLink, useHistory } from 'react-router-dom';

import './Collection.css';

export default function Collection(props) {
  const { status, isLoggedIn } = props;
  const [show, setShow] = useState(false);
  const history = useHistory();

  //status === null => user did not login
  //status.isDone === true => user add movie to finish collection
  //status.isDone === false => user add movie to wishlist collection

  useEffect(() => {}, []);
  const collectionStyle =
    status === null
      ? 'collection-normal'
      : status.isDone
      ? 'collection-done'
      : 'collection-wishlist';

  return (
    <>
      <span
        onMouseEnter={() => setShow(true)}
        onMouseLeave={() => {
          setShow(false);
        }}
        className={`collection-header ${collectionStyle}`}>
        {/* dropdown-header */}
        {!status && (
          <>
            <i className='fa fa-bookmark '></i>
            <span>B su tp</span>
          </>
        )}

        {/* user click " xem" */}
        {status && status.isDone && (
          <>
            <i class='fa fa-check '></i>
            <span> xem</span>
          </>
        )}

        {/* user click "Mun xem" */}
        {status && !status.isDone && (
          <>
            <i class='fa fa-eye '></i>
            <span>Mun xem</span>
          </>
        )}

        {/* show is a flag to open dropdown */}
        {show && (
          <div className='collection-dropdown'>
            {/* user did not login */}
            {!isLoggedIn && (
              <p className='collection-require-login'>
                Xin hy <NavLink to='/auth'>ng nhp</NavLink>  thm phim vo
                b su tp
              </p>
            )}

            {/* user already logined */}
            {isLoggedIn && (
              <>
                {/* user did not take action on collection */}

                <p
                  //arg: movieIsDone
                  onClick={() => props.onClick('addDone')}
                  className='collection-dropdown--item '>
                  Thm vo danh sch <span> xem</span>
                </p>
                <p
                  //arg: movieIsDone
                  onClick={() => props.onClick('addFavorited')}
                  className='collection-dropdown--item'>
                  Thm vo danh sch <span>Mun xem</span>
                </p>
              </>
            )}
          </div>
        )}
      </span>

      {isLoggedIn && status && (
        <>
          <span
            onClick={() => props.onClick('delete')}
            className={`collection-header collection-delete`}>
            <i class='fa fa-trash '></i>
            <span>Xa khi danh sch</span>
          </span>

          <span
            onClick={() => history.push(`/collection`)}
            className='collection-header collection-normal'>
            <i class='fa fa-arrow-alt-circle-right'></i>
            <span>i ti B su tp</span>
          </span>
        </>
      )}
    </>
  );
}
