const genrateYear = () => {
  let years = [
    {
      id: 'all',
      name: '- Tt c -',
    },
  ];
  for (let y = 2021; y >= 2000; y--) {
    years.push({ id: y.toString(), name: y.toString() });
  }
  return years;
};

export const RELEASE_YEAR = genrateYear();

